import Vue from 'vue'
import Vuex from 'vuex'
import decode from 'jwt-decode'
import router from '../router/index'

Vue.use(Vuex)

const store= new Vuex.Store({
  state: {
    token:'',
    usuario:''
  },
  mutations: {
    obtenerUsuario(state, payload) {
      state.token = payload;
      if (payload === '') {
        state.usuario = ''
        router.push({ name: 'Login' });
      } else {
        state.usuario = decode(payload)
        var privilegios=state.usuario.data.rol;
        // console.log(privilegios);
        if(privilegios==='new')
            {router.push({ name: 'Espera' })}
        else if(privilegios==='administrador')
            {router.push({ name: 'Administrar' })}
        else if(privilegios==='imagen')
            {router.push({ name: 'Imagenes' })}
        else if(privilegios==='labpams')
            {router.push({ name: 'LabClinico' })}
        else if(privilegios==='patologia')
            {router.push({ name: 'Patologia' })}
        else if(privilegios==='admision')
            {router.push({ name: 'Trazabilidad' })}
              }                
        }
  },
  actions: {
      guardarUsuario({ commit }, payload) {
      localStorage.setItem('token', payload);
      commit('obtenerUsuario', payload);
    },
    leerToken({commit}){
      const token= localStorage.getItem('token');
      if(token){
        commit('obtenerUsuario',token)
      }else{
        commit('obtenerUsuario','')
      }
      
    },
    cerrarSession(){
      localStorage.removeItem('token');
      //al recargar la apgina automaticamente se borra el historial de rutas y 
      //ya no pueden regresar a la ruta anterior con 
      //las flechas del navegador :V 
      location.reload();
      
      
    },
 
    
  },
  modules: {
  },
  getters:{
    estadoActivo: state => !!state.token,

    
  }
})
export default store;