<template>
  <v-dialog width="500" v-model="registrar" class="ma-0">
    <template v-slot:activator="{ on, attrs }">
      <v-btn
        class="my-5"
        block
        outlined
        v-bind="attrs"
        v-on="on"
        color="red darken-4"
        type="submit"
      >
        Registrarse
      </v-btn>
    </template>

    <v-card class="pa-3 px-4 pa-lg-5">
      <h2 class="text-center red--text text-darken-4">REGISTRARSE</h2>
      <v-form
        @submit.prevent="validateRegistro"
        ref="formularioRegistro"
        lazy-validation
        ma-2
      >
        <v-text-field
          v-model="usuario"
          :rules="userRules "
          color="red darken-4"
          class="my-1"
          label="Nombres y Apellidos"
          hide-details="auto"
        >
        </v-text-field>
        <v-text-field
          v-model="email"
          :rules="emailRules"
          color="red darken-4"
          class="my-1"
          label="E-mail"
        >
        </v-text-field>
        <v-text-field
          color="red darken-4"
          v-model="password"
          class="my-1"
          :rules="passwordRules"
          :type="showPassword ? 'text' : 'password'"
          :append-icon="showPassword ? 'visibility' : 'visibility_off'"
          label="Password"
          @click:append="showPassword = !showPassword"
        >
        </v-text-field>
        <v-text-field
          color="red darken-4"
          v-model="repeatPassword"
          class="my-1"
          :rules="matchRules"
          :type="showRepeatPassword ? 'text' : 'password'"
          :append-icon="showRepeatPassword ? 'visibility' : 'visibility_off'"
          label="Password"
          @click:append="showRepeatPassword = !showRepeatPassword"
        >
        </v-text-field>
        {{modelEmpresa}}
        <v-select
         v-model="modelEmpresa"
        :items="valueArrayEmpresa"
        label="Empresa">
        </v-select>
        <v-alert v-if="fallido.length > 0" dense outlined type="error">
          {{ fallido }}</v-alert
        >
        
        <v-btn
          :disabled="btnDisabled"
          :loading="loading"
          class="my-2"
          block
          dark
          color="red darken-4"
          type="submit"
        >
          Registrarse
        </v-btn>
      </v-form>
    </v-card>
  </v-dialog>
</template>
<script>
export default {
  name: "Registro",
  data() {
    return {
      empresa:[],
      valueArrayEmpresa:[],
      modelEmpresa:'',
      registrar: false,
      fallido: "",
      btnDisabled: false,
      loading: false,
      showPassword: false,
      showRepeatPassword: false,
      usuario: "",
      email: "",
      password: "",
      repeatPassword: "",
      idempresa:0,
      emailRules: [
        (v) => !!v || "E-mail es requerido",
        (v) =>
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(
            v
          ) || "e-mail invalid.",
      ],
      passwordRules: [(v) => !!v || "Se requiere Password"],
      userRules: [(v) => !!v || "Se requiere User"],
      matchRules: [
        (v) => (!!v && v) === this.password || "Las claves no coinciden",
      ],
      FalseRules:[(v)=>{ return v===v }]
    };
  },
  mounted(){
    this.listarEmpresa()
  },
  watch: {
    btnDisabled: () => {
      if (
        this.usuario === "" ||
        this.email === "" ||
        this.email === "" ||
        this.password === "" ||
        this.repeatPassword === ""
      ) {
        return false;
      } else {
        return true;
      }
    }, //vigila cualquier cambio de dato
  },
  methods: {
    loader() {
      this.loading = true;
      setTimeout(() => (this[lod] = false), 3000);
      this.loader = null;
    },
    validateRegistro() {
      var registro = this.$refs.formularioRegistro.validate();
      console.log(this.empresa);
      console.log(this.modelEmpresa);
      var data= this.empresa.filter( ele=>{  if(this.modelEmpresa===ele.descripcion ) return ele});
      console.log(data);
      this.idempresa=data[0].idempresa;
      console.log(this.idempresa)
      if (registro && this.idempresa>0 ) {
        this.loading = true;
        this.axios 
          .post("/saveUsers", {
            usuario: this.usuario.trim(),
            email: this.email.trim(),
            idempresa:this.idempresa,
            password: this.password.trim(),
          
          })
          .then((res) => {           
            if(res.data.mensaje){
            this.$emit('mensajereg');
            this.loading = false;
            this.registrar=false;
            setTimeout(()=>{this.clear()}, 1000);
            }
          })
          .catch((err) => {
            this.loading = false;
            if (err.response.data.err.sqlMessage) {
              this.fallido = err.response.data.err.sqlMessage;
            } else {
              this.fallido = err.response.data.mensaje;
            }
            setTimeout(() => (this.fallido = ""), 2000);
          });
      }
    },
    clear() {  
      this.usuario = '';
      this.email = '';
      this.password = '';
      this.repeatPassword = '';
      this.modelEmpresa = '';
    },
    listarEmpresa(){
        this.axios.get("/listarEmpresa")
        .then( res =>{
           res.data.map(e=>{        
               this.empresa.push(e)
               this.valueArrayEmpresa.push(e.descripcion)
             })
           
           })
        .catch(err=> console.log(err))
    }
  },
};
</script>
<style scoped>
.custom-loader {
  animation: loader 1s infinite;
  display: flex;
}
@-moz-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-webkit-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@-o-keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes loader {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>
